@import "variables";
.animeItem{
  user-select: none;
  width: 200px;
  height: 320px;
  position: relative;
  img{
    object-fit:cover;
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .itemTitleContainer{
    position: absolute;
    width: 100%;
    height: 80px;
    bottom: 0;
    z-index: 10;
    background-color: $firstColor;
  }
  .itemTitle{
    color: $thirdColor;
    font-size: 0.9em;
    padding: 4px 7px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .shadowBox{
    position: absolute;
    z-index: 2;
    width: 100%;
    height: Calc(100% - 80px);;
    top: 0;
    background: linear-gradient(0deg, rgba(35,43,53,1) 0%, rgba(35,43,53, 0.15) 30%);
  }
  .clickBox{
    position: absolute;
    z-index: 3;
    width: 100%;
    height: 240px;
    top: 0;
    background-color: $firstColor;
    opacity: 0%;
    transition: 0.3s;
    color: white;
    font-size: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:hover{
      opacity: 75%;
    }
  }
  .itemDesc{
    display: flex;
    color: $fourthColor;
    font-size: 0.72em;
    margin: 0 10px;
    
    position: absolute;
    bottom: 7px;
    .unicode{
      margin: 0 10px;
    }
  }
}
.Mainpage{
  .mainBanner{
    position: relative;
    width: 100%;
    height: 750px;
    min-height: 450px;
    max-height: calc(100vh - $NavHeight);
    img{
      position: absolute;
      object-fit:cover;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
    .shadowBox{
      color: white;
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      background: linear-gradient(0deg, rgba(23,24,31,1) 0%, rgba(23,24,31, 0.432) 50%);
      .titleBox {
        height: 200px;
        text-align: center;
        user-select: none;
        text-transform: capitalize;
        .title h1{
          font-size: 4.4em;
          margin: 0;
        }
        .desc{
          font-size: 2.5em;
        }
      }
      a{
        width: 270px;
        text-align: center;
        height: 65px;
        background-color: rgba(0, 0, 0, 0.3);
        position: absolute;
        bottom: 0;
        padding-top: 13px;
        font-size: 1.2em;
        text-decoration: none;
        color: $thirdColor;
        border: 3px solid $thirdColor;
        font-weight: 500;
        margin-bottom: 90px;
        transition: 0.4s;
        @extend %button;
        &:hover{
          border-color: $fifthColor;
          background-size: 100% 100%;
        }
      }
    }
  }
  
  .popularAnime{

    width: $defaultWidth;
    margin: 60px auto;
    .animeItem{
      user-select: none;
      width: 200px;
      height: 320px;
      position: relative;
      img{
