        top: 0;
        background: linear-gradient(0deg, rgba(35,43,53,1) 0%, rgba(35,43,53, 0.15) 30%);
      }
      .clickBox{
        position: absolute;
        z-index: 3;
        width: 100%;
        height: 240px;
        top: 0;
        background-color: $firstColor;
        opacity: 0%;
        transition: 0.3s;
        color: white;
        font-size: 4em;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        &:hover{
          opacity: 75%;
