      }
      .itemDesc{
        display: flex;
        color: $fourthColor;
        font-size: 0.72em;
        margin: 0 10px;
        
        position: absolute;
        bottom: 7px;
        .unicode{
          margin: 0 10px;
        }
      }
    }
    .browseContainer{
      .browseTitle{
        color: $thirdColor;
        text-transform: capitalize;
        font-size: 1.5em;
        font-weight: 600;
      }
      .browseDesc{
        color: $sixthColor;
        font-size: 1em;
      }
      .rightSide{
        display: flex;
        align-items: center;
        a{
          text-decoration: none;
          color: $sixthColor;
        }
      }
    }
    .popularAnimeContainer{
      margin-top: 10px;
      padding-top: 20px;
      border-top: 3px solid $fifthColor;
      display: flex;
      justify-content: space-between ;
    }
