}
.randomAnimeContainer{
  max-width: $defaultWidth;
  margin: 80px auto;
  height: 410px;
  background-color: $firstColor;
  display: flex;
  .leftSide{
    position: relative;
    height: 100%;
    width: 290px;
    
    img{
      min-width: 290px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .shadowBox{
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: 3;
      bottom: 0;
      background: linear-gradient(270deg, rgba(35,43,53,1) 0%, rgba(35,43,53,0.16) 30%);
    }
  }
  .rightSide{
    width: 840px;
    position: relative;
    margin: 30px;
    .title{
      color: $thirdColor;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      h1{margin: 0;}
    }
    .desc{
      color: $sixthColor;
      margin-top: 30px;
      font-size: 0.95em;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 7;
      -webkit-box-orient: vertical;
    }
    .bottomContainer{
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      width: 100%;
      justify-content: space-between;
      .bottomDesc{
        display: flex;
        font-size: 0.9em;
        align-items: center;
        color: $fourthColor;
        .unicode{
          margin: 0 15px;
        }
      }
      .button{
        
        width: 250px;
        height: 60px;
        display: flex;
        text-align: center;
        

        font-size: 1.2em;
        color: $thirdColor;
        border: 3px solid $thirdColor;
        font-weight: 500;
        transition: 0.4s;
        @extend %button;
        &:hover{
          border-color: $fifthColor;
          background-size: 100% 100%;
        }
        a{
          padding-top: 10px;
          height: 100%;
          width: 100%;
          color: white;
          text-decoration: none;
         
          
        }
      }
    }
  }
}
